import { Component, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BrandingService } from './branding.service';
import {SpinnerService, ToastService} from '../core';
import { Subscription } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';

@Component({
  selector: 'app-branding',
  templateUrl: './branding.component.html',
  styleUrls: ['./branding.component.less']
})
export class BrandingComponent implements OnInit, OnDestroy {

  public hexaPattern = '^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$';

  public brandingForm: FormGroup = this.formBuilder.group({
    companyLogo: null,
    welcomeText: null,
    navigationBar: this.formBuilder.group({
      backgroundColor: ['#000000', Validators.compose([Validators.required, Validators.pattern(this.hexaPattern)])],
      backgroundEffect: null,
      typeAndIconColor: '#000000',
      icon: null,
    }),
    body: this.formBuilder.group({
      interactiveColor: ['#1393C7', Validators.compose([Validators.required, Validators.pattern(this.hexaPattern)])],
      backgroundColor: ['#FFFFFF', Validators.compose([Validators.required, Validators.pattern(this.hexaPattern)])],
      defaultBodyBranding: false,
      typeAndIconColor: '#000000'
    }),
    tabBar: this.formBuilder.group({
      useNavigationBarTypeAndIconColor: false,
      typeAndIconColor: '#000000'
    }),
    webIcon: null,
    _links: null
  });

  private defaultBrandingSubscription: Subscription = null;

  constructor(
    private formBuilder: FormBuilder,
    private brandingService: BrandingService,
    private spinnerService: SpinnerService,
    private toastService: ToastService
  ) { }

  ngOnInit() {
    this.spinnerService.show();
    this.brandingService.getBranding$().subscribe((response) => {
      this.brandingForm.patchValue(response);
      this.spinnerService.hide();
    }, (error) => {
      this.spinnerService.hide();
    });

    const bodyForm = (this.brandingForm.controls.body) as FormGroup;
    this.defaultBrandingSubscription = bodyForm.controls.defaultBodyBranding.valueChanges
      .pipe(distinctUntilChanged())
      .subscribe((changes) => {
        this.onDefaultValueChanges(changes);
      });
  }

  private onDefaultValueChanges(formValue: boolean): void {
    if (!formValue) {
      const bodyForm = (this.brandingForm.controls.body) as FormGroup;
      bodyForm.controls.backgroundColor.setValue('#F5F5F5');
      bodyForm.controls.typeAndIconColor.setValue('#000000');
    }
  }

  public ngOnDestroy(): void {
    if (this.defaultBrandingSubscription) {
      this.defaultBrandingSubscription.unsubscribe();
    }
  }

  public onLogoUpload(fileId: string): void {
    this.brandingForm.controls.companyLogo.setValue(fileId);
  }

  public onIconUpload(fileId: string): void {
    this.brandingForm.controls.webIcon.setValue(fileId);
  }

  public saveBranding(): void {
    this.spinnerService.show();
    this.brandingService.postBranding$(this.brandingForm.value).subscribe((response) => {
      this.toastService.activateWithTranslate('branding.update.success', 'success');
      this.brandingForm.patchValue(response);
      this.spinnerService.hide();
    }, (error) => {
      this.spinnerService.hide();
      this.toastService.activateWithTranslate('branding.update.error', 'danger');
    });
  }

  public resetSuccess(obj: any): void {
    if (obj.isSuccess) {
      this.toastService.activateWithTranslate(obj.keyString, 'success');
      this.brandingForm.patchValue(obj.branding);
      this.brandingForm.controls.companyLogo.setValue(null);
      this.brandingForm.controls.webIcon.setValue(null);
      this.brandingForm.controls.navigationBar['controls'].icon.setValue(null);
    } else {
      this.toastService.activateWithTranslate(obj.keyString, 'danger');
    }
  }

  public hasRequired(control: AbstractControl): boolean {
    return control.errors && control.errors.required && control.touched;
  }

  public hasError(control: AbstractControl): boolean {
    return control.errors && !control.errors.required && control.invalid;
  }
}
