import { Component, Input, OnChanges } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { SanitizerType } from '../../../shared/sanitizer/sanitizer-type.enum';

@Component({
  selector: 'app-branding-desktop-preview',
  templateUrl: './branding-desktop-preview.component.html',
  styleUrls: ['./branding-desktop-preview.component.less']
})

export class BrandingDesktopPreviewComponent implements OnChanges {

  @Input() public branding: any;

  public readonly urlSanitizer: SanitizerType = SanitizerType.URL;
  public imgSrc: any;

  constructor(private sanitizer: DomSanitizer) { }

  public ngOnChanges(): void {
    if (this.branding && this.branding.navigationBar && this.branding.navigationBar.icon) {
      this.imgSrc = this.branding.navigationBar.icon + window.location.search;
    } else if (this.branding && this.branding.webIcon) {
      this.imgSrc = window.URL.createObjectURL(this.branding.webIcon);
    } else {
      this.imgSrc = './console/assets/graphics/icon.png';
    }
  }
}

