import { Component, EventEmitter, ViewChild, Input, Output, OnChanges } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { SanitizerType } from '../../shared/sanitizer/sanitizer-type.enum';
import { SpinnerService, ToastService } from '../../core';
import { BrandingService } from '../branding.service';

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['./file-upload.component.less'],
})

export class FileUploadComponent implements OnChanges {

  @ViewChild('file')public file;
  @Input()public isIcon: boolean;
  @Output()public ImageUpload = new EventEmitter<File>();
  @Input()public branding: any;

  public imgSrc: any;
  public readonly urlSanitizer: SanitizerType = SanitizerType.URL;
  public errorMsg;
  private isLocalImage: boolean;

  constructor(
    private sanitizer: DomSanitizer,
    private spinnerService: SpinnerService,
    private brandingService: BrandingService,
    private toastService: ToastService,
  ) { }

  public ngOnChanges(): void {
    if (this.branding && this.branding.companyLogo &&
      !this.isIcon && !this.isLocalImage) {
      const url = this.branding._links['companylogo-url'].href;
      this.imgSrc = url + window.location.search;
    }

    if (this.branding && this.branding.navigationBar && this.branding.navigationBar.icon &&
      this.isIcon && !this.isLocalImage) {
      this.imgSrc = this.branding.navigationBar.icon + window.location.search;
    } else if (this.branding && !this.branding.webIcon && this.isIcon) {
      this.imgSrc = './console/assets/graphics/icon.png';
    } else if (this.branding && !this.branding.companyLogo && !this.isIcon) {
      this.imgSrc = './console/assets/graphics/company-logo.png';
    }
  }

  public addFiles(): void {
    this.file.nativeElement.click();
  }

  private confirmImage(file: File): void {
    if (file && !file.type.match('image.png|image.jpeg|image.jpg|image.gif')) {
      this.errorMsg = 'branding.image.type.error';
      setTimeout(() => {
        this.errorMsg = '';
      }, 3000);
    } else {
      this.errorMsg = '';
    }
  }

  public handleUpload(file: File): void {
    if (!file ) {
      return;
    }
    this.confirmImage(file);
    if (!this.errorMsg) {
      const url = this.branding._links['upload-companylogo'].href;
      this.spinnerService.show();
      this.brandingService.uploadImage$(url, file)
        .subscribe((response) => {
        this.spinnerService.hide();
        this.isLocalImage = true;
        this.imgSrc = window.URL.createObjectURL(file);
        this.ImageUpload.emit(response);
      }, (error) => {
        this.spinnerService.hide();
        if (error && typeof error === 'string') {
          const errorMsg = JSON.parse(error);
          if (errorMsg && errorMsg.code && errorMsg.message) {
            this.toastService.activate(errorMsg.message, 'danger');
          }
        }
      });
    }
  }

}
