import {Component, EventEmitter, Output} from '@angular/core';
import {BrandingService} from '../branding.service';
import {SpinnerService} from '../../core';

@Component({
  selector: 'app-branding-reset',
  templateUrl: './branding-reset.component.html',
  styleUrls: []
})

export class BrandingResetComponent {

  @Output() public resetSuccess = new EventEmitter<any>();
  public openModal = false;

  constructor(
    private brandingService: BrandingService,
    private spinnerService: SpinnerService,
  ) {
  }

  public open(): void {
    this.openModal = true;
  }

  public resetBranding(): void {
    this.spinnerService.show();
    this.openModal = false;
    this.brandingService.resetBranding$().subscribe((response) => {
      this.spinnerService.hide();
      this.resetSuccess.emit({keyString: 'branding.reset.success', isSuccess: true, branding: response});
    }, (error) => {
      this.resetSuccess.emit({keyString: 'branding.update.error', isSuccess: false});
      this.spinnerService.hide();
    });
  }
}
